body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth; }

body {
  text-align: left;
  font-family: 'Avenir', sans-serif; }

h1, h2 {
  color: #448483; }

h1 {
  font-size: 4.5rem;
  margin-top: 0rem; }

.block {
  margin-top: 2rem; }

.paragraph {
  margin: 15vw 5vw; }

.title {
  font-size: 3.5rem; }

.black {
  color: #000000; }

.light {
  font-weight: 300; }

.italic {
  font-style: italic; }

.big-picture {
  max-width: 100%; }

@media screen and (min-width: 576px) {
  .title {
    font-size: 4rem; }
  .paragraph {
    margin: 15vw; } }

@media screen and (min-width: 768px) {
  .title {
    font-size: 5rem; }
  .block {
    margin-top: 6rem; } }

.header {
  min-height: 10vh;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  border-bottom: 2px solid #448483;
  margin-bottom: 4rem; }

.links {
  position: absolute;
  right: 2rem;
  bottom: 0px; }

a {
  font-size: 1.2rem;
  font-weight: 600;
  text-decoration: none;
  color: #448483; }

a:hover {
  color: #4ADAE8;
  text-decoration: none; }

.header-link {
  margin: 0rem 0rem 0rem 1rem; }

.favicon {
  height: 3rem;
  position: absolute;
  left: 2rem;
  margin: 0rem; }

@media screen and (min-width: 768px) {
  .header {
    margin-bottom: 9rem; }
  .favicon {
    left: 8rem; }
  .links {
    right: 8rem; }
  .header-link {
    margin: 0rem 0rem 0rem 4rem; } }

.footer {
  margin-top: 20vh;
  padding: 1rem;
  color: #fff;
  background: #448483; }

.footerh2 {
  color: #fff;
  font-size: 1.5rem; }

.footer-text {
  font-size: 1rem;
  font-weight: 600; }

@media screen and (min-width: 576px) {
  .footer-text {
    font-size: 1rem;
    font-weight: 700; }
  p {
    margin-bottom: 0rem; } }

@media screen and (min-width: 768px) {
  .footer-text {
    text-align: center; } }

.about-row {
  margin: 0; }

.about-title {
  font-size: 3rem;
  margin: 2rem 0rem 1rem 0rem; }

.text {
  margin: 0;
  font-size: 1.25rem; }

@media screen and (min-width: 576px) {
  .about-title {
    font-size: 4rem; }
  .text-link {
    font-size: 1.25rem;
    margin: 0;
    margin-left: 0.3rem; } }

@media screen and (min-width: 768px) {
  .about-row {
    margin-left: 6rem;
    margin-right: 6rem; }
  .about-title {
    margin: 2rem 0rem 1rem 3rem; }
  .text {
    margin-left: 3rem;
    font-size: 1.25; } }

.title {
  text-align: left;
  font-size: 2rem;
  font-weight: 800;
  margin-left: 2rem;
  margin-top: 0rem; }

.intro {
  margin: 0rem 2rem;
  font-weight: 500;
  font-size: 1.25rem; }

.social-media {
  margin-left: 2rem; }
  .social-media .social-media-icon {
    height: 1.5rem;
    margin: 0rem 2rem 0rem 0rem; }

.scroll-down {
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite; }

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px); }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px); } }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px); }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px); } }

.projects {
  padding: 0;
  overflow: hidden; }

.project a {
  margin: 0; }

@media screen and (min-width: 576px) {
  .title {
    font-size: 3rem; }
  .black {
    color: #000000; }
  .text-link {
    font-size: 1.5rem;
    margin-left: 0.3rem; }
  .social-media .social-media-icon {
    text-align: left;
    height: 1.5rem; } }

@media screen and (min-width: 768px) {
  .title {
    font-size: 5rem;
    margin-left: 8rem; }
  .intro {
    margin: 0rem 8rem 2rem 8rem;
    font-size: 1.5rem; }
  .social-media {
    margin-left: 8rem; }
    .social-media a {
      margin: 0; }
  .homepage-row {
    margin-left: 8rem;
    margin-right: 8rem; } }

@media screen and (min-width: 992px) {
  .intro {
    margin: 0rem 20rem 2rem 8rem; }
  .project img {
    width: 25vw; } }

h1 {
  font-size: 3rem; }

h3 {
  font-size: 2.5rem;
  margin-bottom: 4rem; }

.case-title {
  margin-left: 2rem; }

.case-subtitle {
  margin-left: 2rem; }

@media screen and (min-width: 576px) {
  h1 {
    font-size: 6rem; }
  .case-subtitle {
    text-align: left;
    margin-left: 2rem; }
  .paragraph {
    text-align: left;
    margin-left: 2rem; } }

@media screen and (min-width: 768px) {
  .case-title {
    margin-left: 8rem; }
  .case-subtitle {
    margin-left: 8rem; }
  .paragraph {
    font-size: 1.2rem;
    margin-left: 8rem; } }

