.title {
  text-align: left;
  font-size: 2rem;
  font-weight: 800;
  margin-left: 2rem;
  margin-top: 0rem; }

.intro {
  margin: 0rem 2rem;
  font-weight: 500;
  font-size: 1.25rem; }

.social-media {
  margin-left: 2rem; }
  .social-media .social-media-icon {
    height: 1.5rem;
    margin: 0rem 2rem 0rem 0rem; }

.scroll-down {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite; }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-30px); }
  60% {
    transform: translateY(-15px); } }

.projects {
  padding: 0;
  overflow: hidden; }

.project a {
  margin: 0; }

@media screen and (min-width: 576px) {
  .title {
    font-size: 3rem; }
  .black {
    color: #000000; }
  .text-link {
    font-size: 1.5rem;
    margin-left: 0.3rem; }
  .social-media .social-media-icon {
    text-align: left;
    height: 1.5rem; } }

@media screen and (min-width: 768px) {
  .title {
    font-size: 5rem;
    margin-left: 8rem; }
  .intro {
    margin: 0rem 8rem 2rem 8rem;
    font-size: 1.5rem; }
  .social-media {
    margin-left: 8rem; }
    .social-media a {
      margin: 0; }
  .homepage-row {
    margin-left: 8rem;
    margin-right: 8rem; } }

@media screen and (min-width: 992px) {
  .intro {
    margin: 0rem 20rem 2rem 8rem; }
  .project img {
    width: 25vw; } }
