h1 {
  font-size: 3rem; }

h3 {
  font-size: 2.5rem;
  margin-bottom: 4rem; }

.case-title {
  margin-left: 2rem; }

.case-subtitle {
  margin-left: 2rem; }

@media screen and (min-width: 576px) {
  h1 {
    font-size: 6rem; }
  .case-subtitle {
    text-align: left;
    margin-left: 2rem; }
  .paragraph {
    text-align: left;
    margin-left: 2rem; } }

@media screen and (min-width: 768px) {
  .case-title {
    margin-left: 8rem; }
  .case-subtitle {
    margin-left: 8rem; }
  .paragraph {
    font-size: 1.2rem;
    margin-left: 8rem; } }
